import $ from 'jquery';
import store from 'store';
import vs from '../core';
import * as editor from '../editor';
import AMDLoader from '../common/AMDLoader';
import {bindPageDialog} from '../dialogs';

vs.init = function () {
    //
    bindPageDialog();

    //
    editor.bindLayout();

    //AMD js
    AMDLoader(function () {

        //AMDjquery
        window.define('jquery', function () {
            return $;
        })

        editor.bindEditor();
      
    });

};


